

// Fonts
//
// Typefaces and fonts used across the site.
// They are never used directly but set up properly with the `text-style` mixin.
//
// Styleguide fonts


// Roboto
// - The default typeface, sans-serif
// - character set: Latin
// - served from Google
// - styles and weights: Regular, Medium, Bold, 

@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,);


// Lato
// - The headings typeface, sans-serif
// - character set: Latin
// - served from Google
// - styles and weights: Bold, Black, 

@import url(https://fonts.googleapis.com/css?family=Lato:700,900,);


// Roboto condensed
// - Glyph typeface, sans-serif
// - character set: Latin
// - served from Google
// - styles and weights: Regular, 

@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:700,);




// The font mixin
@mixin font($variant) {

  @if ($variant == 'roboto-regular') {
    font-family: 'roboto', sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  @if ($variant == 'roboto-medium') {
    font-family: 'roboto', sans-serif;
    font-weight: 500;
    font-style: normal;
  }
  
  @if ($variant == 'roboto-bold') {
    font-family: 'roboto', sans-serif;
    font-weight: 700;
    font-style: normal;
  }
  
  @if ($variant == 'lato-bold') {
    font-family: 'lato', sans-serif;
    font-weight: 700;
    font-style: bold;
  }
  
  @if ($variant == 'lato-black') {
    font-family: 'lato', sans-serif;
    font-weight: 900;
    font-style: normal;
  }
  
  @if ($variant == 'roboto-condensed-regular') {
    font-family: 'roboto-condensed', sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
}


// Font classes

.roboto-regular {
  font-family: 'roboto', sans-serif;
  font-weight: 400;
  font-style: normal;
}

.roboto-medium {
  font-family: 'roboto', sans-serif;
  font-weight: 500;
  font-style: normal;
}

.roboto-bold {
  font-family: 'roboto', sans-serif;
  font-weight: 700;
  font-style: normal;
}

.lato-bold {
  font-family: 'lato', sans-serif;
  font-weight: 700;
  font-style: bold;
}

.lato-black {
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-style: normal;
}

.roboto-condensed-regular {
  font-family: 'roboto-condensed', sans-serif;
  font-weight: 400;
  font-style: normal;
}

