@mixin everything {
  @include section-fitscreen;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  &__hero {
    @include everything__hero;
  }

  .articles {
    @include articles;
  }

  .article {
    &__subtitle, {
      padding: $lem / 4 0;
    }

    &__button, {
      padding: $lem 0;
    }
  }
}
