@mixin contact {
  display: flex;
  flex-direction: column;

  &__title {
    display: flex;

    @include text-style(headline-3);
    padding: $lem / 4 0;
  }

  .ul {
    @include ul;
    @include ul--horizontal;

    justify-content: flex-start;
  }

  .articles {
    @include articles(2);
  }

  .article {
    &__title {
      @include text-style(default);
      font-size: larger !important;
    }
  }
}
