// Colors
//
// The colors together with the color pairs.
// Colors don't exist alone yet in pairs, like black on white.
// All color pairs have a contrast ratio set for perfect readability using [Lea Verou's contrast checking tool](http://leaverou.github.io/contrast-ratio/).
//
// Styleguide colors

// Main color
$white: #fff;

// Main color
$black: #292c31;

// Main color
$blue: #00c0d8;

// Main color
$green: #2de79e;

// Action color
$red: #f8425b;

// Secondary color
$gray1: #7a8694;


// Color combinations. Colors are not used standalone but in pairs
@mixin colors($name) {

  @if ($name == 'white-on-black') {
    color: $white;
    background-color: $black;
    
  }

  @if ($name == 'black-on-white') {
    color: $black;
    background-color: $white;
    
  }

  @if ($name == 'white-on-blue') {
    color: $white;
    background-color: $blue;
    
  }

  @if ($name == 'blue-on-white') {
    color: $blue;
    background-color: $white;
    
  }

  @if ($name == 'white-on-green') {
    color: $white;
    background-color: $green;
    
  }

  @if ($name == 'green-on-white') {
    color: $green;
    background-color: $white;
    
  }

  @if ($name == 'white-on-red') {
    color: $white;
    background-color: $red;
    
  }

  @if ($name == 'gray1-on-white') {
    color: $gray1;
    background-color: $white;
    
  }

  @if ($name == 'gray1-on-black') {
    color: $gray1;
    background-color: $black;
    
  }

}

// Color classes

.white {
  background: #fff;
};

.black {
  background: #292c31;
};

.blue {
  background: #00c0d8;
};

.green {
  background: #2de79e;
};

.red {
  background: #f8425b;
};

.gray1 {
  background: #7a8694;
};


// Color pair classes

.white-on-black {
  color: $white;
  background-color: $black;
  
}

.black-on-white {
  color: $black;
  background-color: $white;
  
}

.white-on-blue {
  color: $white;
  background-color: $blue;
  
}

.blue-on-white {
  color: $blue;
  background-color: $white;
  
}

.white-on-green {
  color: $white;
  background-color: $green;
  
}

.green-on-white {
  color: $green;
  background-color: $white;
  
}

.white-on-red {
  color: $white;
  background-color: $red;
  
}

.gray1-on-white {
  color: $gray1;
  background-color: $white;
  
}

.gray1-on-black {
  color: $gray1;
  background-color: $black;
  
}

